/* Postions */
.fixed {
    position: fixed;
}

.relative {
    position: relative;
}

.absolute {
    position: absolute;
}

.static {
    position: static;
}

/* Zindex*/
.zindex-1 {
    z-index: 1;
}

.zindex-2 {
    z-index: 2;
}

.zindex-3 {
    z-index: 3;
}

/* Borders */
.border-all {
    border: 1px solid var(--global--color-primary);
}

.border-top {
    border-top: 1px solid var(--global--color-primary);
}

.border-bottom {
    border-bottom: 1px solid var(--global--color-primary);
}

.border-right {
    border-right: 1px solid var(--global--color-primary);
}

.border-left {
    border-left: 1px solid var(--global--color-primary);
}

/* Display */
.inline {
    display: inline;
}

.block {
    display: block;
}

.inline-block {
    display: inline-block;
}

.hide {
    display: none;
}

.flex {
    display: flex;
}

@media only screen and (max-width: 767px) {

    .text-center-xs {
        text-align: center !important;
    }

    .pull-none-xs {
        float     : none !important;
        text-align: center !important;
    }

    .mb-15-xs {
        margin-bottom: 15px;
    }

    .mb-30-xs {
        margin-bottom: 30px !important;
    }

    .mb-40-xs {
        margin-bottom: 40px !important;
    }

    .mb-50-xs {
        margin-bottom: 50px;
    }

    .mb-60-xs {
        margin-bottom: 60px !important;
    }

    .p-none-xs {
        padding-right: 0;
        padding-left : 0;
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    .text-center-sm {
        text-align: center !important;
    }

    .mb-0-sm {
        margin-bottom: 0;
    }

    .mb-15-sm {
        margin-bottom: 15px;
    }

    .mb-30-sm {
        margin-bottom: 30px !important;
    }

    .mb-40-sm {
        margin-bottom: 40px;
    }

    .mb-50-sm {
